@keyframes rubberBand {
  from {
    transform: scale3d(1, 1, 1);
  }

  30% {
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    transform: scale3d(0.75, 1.45, 1);
  }

  50% {
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    transform: scale3d(1.05, 0.95, 1);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

#projects {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 40px;
  background: rgb(30, 24, 46);
  background: linear-gradient(
    90deg,
    rgba(30, 24, 46, 1) 42%,
    rgba(23, 30, 55, 1) 77%
  );

  .projects-header {
    text-align: center;
    margin-bottom: 40px;

    .portfolio-box {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin-bottom: 20px;

      .portfolio,
      .previous {
        display: flex;
        justify-content: center;

        h1 {
          color: #f9f5ff;
          font-weight: bold;
          font-size: 60px;
          cursor: pointer;
          margin-bottom: 0px;
          transition: all 0.3s ease;

          &:hover {
            color: var(--accent-color, #a21eff);
            animation: rubberBand 1s infinite;
          }
        }
      }
    }

    .projects-title {
      display: flex;
      justify-content: center;

      h1 {
        color: #f9f5ff;
        font-size: 60px;
        font-weight: bold;
        cursor: pointer;
        margin-bottom: 0px;
        transition: all 0.3s ease;

        &:hover {
          color: #ff3870;
          animation: rubberBand 1s infinite;
        }
      }
    }
  }

  .projects-intro {
    font-size: 20px;
    color: #f9f5ff;
    padding: 24px 0;
    text-align: center;
    max-width: 800px;
    margin: 0 auto 60px;

    a {
      color: var(--accent-color, #a21eff);
      transition: color 0.3s ease;

      &:hover {
        color: var(--primary-green, #1fea64);
        text-decoration: underline;
      }
    }
  }

  .projects-horizontal-container {
    width: 100%;
    overflow: hidden;
    margin-bottom: 60px;
  }

  .projects-grid {
    display: flex;
    gap: 30px;
    width: fit-content;
    padding: 0 20px;
    will-change: transform;
  }

  .project-card {
    background: rgba(29, 30, 44, 0.6);
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    border: 2px solid transparent;
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    width: 400px;
    min-width: 300px;

    &:hover {
      transform: translateY(-10px);
      border-color: var(--primary-green, #1fea64);
      box-shadow: 0 20px 40px rgba(31, 234, 100, 0.3);

      .card-image {
        .card-overlay {
          opacity: 1;
        }

        img {
          transform: scale(1.1);
        }
      }

      .card-content {
        .card-title {
          color: var(--primary-green, #1fea64);
        }
      }
    }

    .card-image {
      width: 100%;
      height: 250px;
      overflow: hidden;
      position: relative;
      background: linear-gradient(
        135deg,
        rgba(30, 24, 46, 1),
        rgba(23, 30, 55, 1)
      );

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.5s ease;
      }

      .card-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(162, 30, 255, 0.85);
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0;
        transition: opacity 0.3s ease;

        .view-more {
          color: white;
          font-size: 20px;
          font-weight: bold;
          text-transform: uppercase;
          letter-spacing: 2px;
        }
      }
    }

    .card-content {
      padding: 30px;
      flex: 1;
      display: flex;
      flex-direction: column;

      .card-category {
        color: var(--accent-color, #a21eff);
        font-size: 12px;
        text-transform: uppercase;
        letter-spacing: 2px;
        margin-bottom: 15px;
        font-weight: bold;
      }

      .card-title {
        color: #f9f5ff;
        font-size: 28px;
        font-weight: bold;
        margin-bottom: 15px;
        transition: color 0.3s ease;
      }

      .card-description {
        color: #f9f5ff;
        font-size: 16px;
        line-height: 1.6;
        margin-bottom: 20px;
        flex: 1;
        opacity: 0.9;
      }

      .card-actions {
        margin-top: auto;

        .card-btn {
          display: inline-block;
          padding: 12px 30px;
          background: linear-gradient(
            135deg,
            var(--primary-green, #1fea64),
            #1fea64
          );
          color: #1d1e2c;
          border-radius: 25px;
          text-decoration: none;
          font-weight: bold;
          font-size: 14px;
          transition: all 0.3s ease;
          border: 2px solid transparent;

          &:hover {
            background: transparent;
            color: var(--primary-green, #1fea64);
            border-color: var(--primary-green, #1fea64);
            transform: translateY(-2px);
            box-shadow: 0 10px 20px rgba(31, 234, 100, 0.3);
          }
        }
      }
    }
  }

  .projects-footer {
    text-align: center;
    margin-top: 40px;

    .see-all-projects {
      display: inline-flex;
      align-items: center;
      gap: 10px;
      font-size: 24px;
      color: var(--primary-green, #1fea64);
      text-decoration: none;
      transition: all 0.3s ease;
      padding: 15px 40px;
      border: 2px solid var(--primary-green, #1fea64);
      border-radius: 30px;

      .arrow {
        transition: transform 0.3s ease;
        display: inline-block;
      }

      &:hover {
        background: var(--primary-green, #1fea64);
        color: #1d1e2c;
        transform: translateY(-3px);
        box-shadow: 0 10px 30px rgba(31, 234, 100, 0.4);

        .arrow {
          transform: translateX(5px);
        }
      }
    }
  }
}

// Responsive Design
@media (max-width: 1200px) {
  #projects {
    .project-card {
      width: 350px;
      min-width: 280px;
    }
  }
}

@media (max-width: 900px) {
  #projects {
    .project-card {
      width: 300px;
      min-width: 250px;
    }

    .projects-header {
      .portfolio-box,
      .projects-title {
        h1 {
          font-size: 40px;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  #projects {
    padding: 40px 20px;

    .project-card {
      width: 280px;
      min-width: 250px;
    }

    .projects-header {
      .portfolio-box,
      .projects-title {
        h1 {
          font-size: 30px;
        }
      }
    }

    .projects-intro {
      font-size: 16px;
    }
  }
}

// Khn stillr (backward compatibility)
.Snobella,
.Calculator {
  display: none;
}
